import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {layerEvents} from '@servicenow/library-enhanced-utils';
import {actionTypes} from '@servicenow/ui-core';
import styles from './_now-tooltip.scss';
import {getBestFitInfo} from '@servicenow/behavior-fit';
import {schedule} from './scheduler';

const {
	COMPONENT_CONNECTED,
	COMPONENT_DOM_READY,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const listenerMap = new WeakMap();

const handleKeydown = (dispatch, event) => {
	if (event.key === 'Escape') {
		event.stopPropagation();
		dispatch(() => {
			return {
				type: 'NOW_TOOLTIP#OPENED_SET',
				payload: {value: false, target: 'keyboard'}
			};
		});
	}
};

const addTargetListeners = (targetRef, componentId, delay, dispatch) => {
	if (!targetRef || listenerMap.has(targetRef)) return;
	const showTooltip = () => {
		schedule(
			componentId,
			() =>
				dispatch(() => {
					return {
						type: 'NOW_TOOLTIP#OPENED_SET',
						payload: {value: true, target: 'target'}
					};
				}),
			delay.show || delay
		);
	};

	const hideTooltip = () => {
		schedule(
			componentId,
			() =>
				dispatch(() => {
					return {
						type: 'NOW_TOOLTIP#OPENED_SET',
						payload: {value: false, target: 'target'}
					};
				}),
			delay.hide || delay
		);
	};
	targetRef.addEventListener('mouseover', showTooltip, false);
	targetRef.addEventListener('mouseout', hideTooltip, false);
	targetRef.addEventListener('focus', showTooltip, false);
	targetRef.addEventListener('blur', hideTooltip, false);

	listenerMap.set(targetRef, {showTooltip, hideTooltip});
};

const removeTargetListeners = (targetRef, id) => {
	if (!targetRef || !listenerMap.has(targetRef)) return;
	const {showTooltip, hideTooltip} = listenerMap.get(targetRef);
	targetRef.removeEventListener('mouseover', showTooltip, false);
	targetRef.removeEventListener('mouseout', hideTooltip, false);
	targetRef.removeEventListener('focus', showTooltip, false);
	targetRef.removeEventListener('blur', hideTooltip, false);

	if (id) layerEvents.remove(id);
	listenerMap.delete(targetRef);
};

const view = (state, {dispatch}) => {
	const {
		componentId,
		properties: {delay, opened}
	} = state;

	return (
		<span
			id={`tooltip-${componentId}`}
			className="now-tooltip"
			aria-hidden="true"
			on-mouseover={() => {
				schedule(
					componentId,
					() =>
						dispatch(() => {
							return {
								type: 'NOW_TOOLTIP#OPENED_SET',
								payload: {value: true, target: 'content'}
							};
						}),
					delay.show || delay
				);
			}}
			on-mouseout={() => {
				schedule(
					componentId,
					() =>
						dispatch(() => {
							return {
								type: 'NOW_TOOLTIP#OPENED_SET',
								payload: {
									value: false,
									target: 'content'
								}
							};
						}),
					delay.hide || delay
				);
			}}>
			{/* state.properties.content is added here */}
			<slot
				on-slotchange={
					opened
						? dispatch.bind(null, () => ({type: 'PRIVATE#CHECK_FIT'}))
						: null
				}>
				<span className="now-line-height-crop" />
			</slot>
		</span>
	);
};

const FIT_CONSTRAIN = {minHeight: 24, maxWidth: 304};

const fitTooltip = ({action, host, properties}) => {
	const {
		meta: {id}
	} = action;
	const {
		targetRef: target,
		container,
		position,
		offset,
		opened,
		content: label
	} = properties;
	const tooltipElement = host.shadowRoot.querySelector(`#tooltip-${id}`);
	const slottedElements = tooltipElement.querySelector('slot').assignedNodes();
	const haveTooltipContent = Boolean(label || slottedElements.length);
	const tooltipVisible = Boolean(
		opened && target && tooltipElement && haveTooltipContent
	);

	if (tooltipVisible) {
		tooltipElement.querySelector('.now-line-height-crop').textContent = label;

		tooltipElement.removeAttribute('style');
		const fitStyle = getBestFitInfo({
			target,
			content: tooltipElement,
			container,
			position,
			offset: Array.isArray(offset)
				? offset
				: [0, 0, offset, offset, offset, offset],
			constrain: FIT_CONSTRAIN
		}).style;

		Object.assign(tooltipElement.style, fitStyle, {
			visibility: 'visible',
			height: null
		});
	} else if (tooltipElement) {
		tooltipElement.removeAttribute('style');
	}
};

/**
 * Descriptive text that appears when the user hovers over or tabs to an element.
 *
 * ```jsx
 * import {createCustomElement} from '@servicenow/ui-core';
 * import snabbdom, {Fragment} from '@servicenow/ui-renderer-snabbdom';
 * import '@servicenow/now-tooltip';
 *
 * const view = (state, {updateState}) => {
 *   const setTargetRef = (ref) => setTimeout(() => updateState({targetRef: ref}));
 *   return (
 *     <Fragment>
 *       <span
 *         hook-insert={({elm}) => setTargetRef(elm)}
 *         hook-destroy={() => setTargetRef(null)}
 *         aria-describedby="tooltip1">
 *           Hover over me for a tooltip
 *       </span>
 *       <now-tooltip
 *         id="tooltip1"
 *         target-ref={state.targetRef}
 *         content="This text will appear in the tooltip"
 *         delay={500}
 *       />
 *     </Fragment>
 *   );
 * };
 *
 * createCustomElement('sn-text-with-tooltip', {
 *   renderer: {
 *     type: snabbdom,
 *     view
 *   }
 * });
 * ```
 *
 * Alternatively, you can slot in content.
 *
 * ```jsx
 * <now-tooltip
 *    id="tooltip2"
 *    target-ref={state.targetRef}>
 *    I am <strong>rich content</strong>
 * </now-tooltip
 *
 * @seismicElement now-tooltip
 * @summary Descriptive text to inform the user of the purpose of or context for an element.
 */
createEnhancedElement('now-tooltip', {
	properties: {
		/**
		 * The target HTML element that the tooltip appears over when
		 * the user hovers over or tabs to it.
		 * @type {HTMLElement}
		 */
		targetRef: {},
		/**
		 * Text that displays within the tooltip. If you want to display complex content,
		 * use the default slot instead. This property is ignored when the slot is used.
		 * @type {string}
		 */
		content: {schema: {type: 'string'}},
		/**
		 * True when the user hovers over or tabs to the tooltip's target element.
		 * Use `manage-opened` to override the default behavior and handle the
		 * `NOW_TOOLTIP#OPENED_SET` action manually.
		 * @type {boolean}
		 */
		opened: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * Time delay in milliseconds after the user interacts with the
		 * target element before the the tooltip appears or
		 * disappears. Can be specified as an object with `show` and `hide`
		 * keys, or as a simple number that applies to both.
		 * @type {(number | {show: number, hide: number})}
		 */
		delay: {
			default: 300,
			schema: {
				oneOf: [
					{type: 'number'},
					{
						type: 'object',
						properties: {show: {type: 'number'}, hide: {type: 'number'}}
					}
				]
			}
		},
		/**
		 * Element that the tooltip must fit within.
		 *
		 * @type {HTMLElement}
		 */
		container: {},
		/**
		 * Array of possible positions to place the tooltip relative to its target element.
		 * @type {Array<string>}
		 */
		position: {
			default: [
				'top-center bottom-center',
				'bottom-center top-center',
				'center-end center-start',
				'center-start center-end',
				'top-end top-start',
				'bottom-end bottom-start',
				'top-start top-end',
				'bottom-start bottom-end'
			],
			schema: {type: 'array', items: {type: 'string'}}
		},
		/**
		 * Number of pixels to shift the tooltip from its target element.
		 * @type {number}
		 * @private
		 */
		offset: {
			default: 8,
			schema: {
				oneOf: [{type: 'number'}, {type: 'array', items: {type: 'number'}}]
			}
		},
		/** @private */
		ariaLabel: {
			reflect: true,
			computed: ({properties: {content}}) => content
		},
		/** @private */
		ariaHidden: {
			reflect: true,
			computed: () => 'true'
		},
		/** @private */
		role: {
			reflect: true,
			default: 'tooltip'
		}
	},
	initialState: {
		fitStyle: {contentArea: {}, content: {}}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({action, properties, dispatch, host}) => {
			const {
				meta: {id}
			} = action;
			const {targetRef, delay, opened} = properties;
			addTargetListeners(targetRef, id, delay, dispatch);

			if (opened && !listenerMap.has(host)) {
				const boundHandler = (event) => handleKeydown(dispatch, event);
				layerEvents.add({
					id,
					events: ['keydown'],
					callback: boundHandler
				});
				listenerMap.set(host, boundHandler);
			}
		},
		[COMPONENT_DISCONNECTED]: ({action, properties}) => {
			removeTargetListeners(properties.targetRef, action.meta.id);
		},
		[COMPONENT_DOM_READY]: ({properties, host, action}) => {
			fitTooltip({properties, host, action});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({action, host, properties, dispatch}) => {
			const {
				payload: {name, value, previousValue},
				meta: {id}
			} = action;
			if (
				name === 'content' ||
				name === 'container' ||
				name === 'position' ||
				name === 'offset' ||
				name === 'opened'
			) {
				fitTooltip({properties, host, action});
			}
			if (name === 'targetRef') {
				const {delay} = properties;
				removeTargetListeners(previousValue);
				addTargetListeners(value, id, delay, dispatch);
				fitTooltip({properties, host, action});
			}
			if (name === 'opened') {
				if (value && !listenerMap.has(host)) {
					const boundHandler = (event) => handleKeydown(dispatch, event);
					layerEvents.add({
						id,
						events: ['keydown'],
						callback: boundHandler
					});
					listenerMap.set(host, boundHandler);
				} else if (!value && listenerMap.has(host)) {
					layerEvents.remove(id);
					listenerMap.delete(host);
				}
			}
		},
		'PRIVATE#CHECK_FIT': {
			stopPropagation: true,
			modifier: {
				type: 'throttle',
				limit: 10
			},
			effect: ({action, host, properties}) => {
				fitTooltip({properties, host, action});
			}
		}
	},
	view,
	styles,
	dispatches: {
		/**
		 * Dispatched when the tooltip opens or closes.
		 * Set the `manage-opened` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 */
		'NOW_TOOLTIP#OPENED_SET': {}
	},
	slots: {
		/**
		 * Complex content to display inside the tooltip body.
		 * When this slot is used, the `content` property is ignored.
		 *
		 * If the content is plain text,
		 * use the `content` property instead.
		 * @defaultSlot
		 */
		defaultSlot: {}
	}
});
